'use client'

import { useEffect, useState } from 'react'
import {
  ChevronDown,
  ChevronRight,
  Plus,
  Users,
  Briefcase,
  Pencil,
  Trash2,
  X,
} from 'lucide-react'

function moeda(valor: number) {
  return (valor || 0).toLocaleString('pt-BR', {
    style: 'currency',
    currency: 'BRL',
  })
}

export default function VinculacaoOrganizacionalPage() {
  const [hierarquia, setHierarquia] = useState<any[]>([])
  const [departamentos, setDepartamentos] = useState<any[]>([])
  const [funcoes, setFuncoes] = useState<any[]>([])
  const [usuarios, setUsuarios] = useState<any[]>([])
  const [openDptos, setOpenDptos] = useState<number[]>([])
  const [openFuncoes, setOpenFuncoes] = useState<number[]>([])
  const [showModal, setShowModal] = useState(false)
  const [editando, setEditando] = useState<any | null>(null)

  const [form, setForm] = useState({
    vinculacao_id: null as number | null,
    dpto_id: '',
    func_id: '',
    user_id: '',
    percentual_alocacao: 100,
    custo_mensal: '',
  })

  useEffect(() => {
    carregarHierarquia()
    carregarCombos()
  }, [])

  async function carregarHierarquia() {
    const token = localStorage.getItem('entity_token')
    const res = await fetch(
      `${process.env.NEXT_PUBLIC_API_URL}/entity/vinculacao`,
      { headers: { Authorization: `Bearer ${token}` } },
    )
    setHierarquia(await res.json())
  }

  async function carregarCombos() {
    const token = localStorage.getItem('entity_token')
    const res = await fetch(
      `${process.env.NEXT_PUBLIC_API_URL}/entity/vinculacao/combos`,
      { headers: { Authorization: `Bearer ${token}` } },
    )
    const json = await res.json()
    setDepartamentos(json.departamentos || [])
    setFuncoes(json.funcoes || [])
    setUsuarios(json.usuarios || [])
  }

  async function excluir(vinculacao_id: number) {
    if (!confirm('Deseja realmente excluir este vÃ­nculo?')) return

    const token = localStorage.getItem('entity_token')

    await fetch(
      `${process.env.NEXT_PUBLIC_API_URL}/entity/vinculacao/${vinculacao_id}`,
      {
        method: 'DELETE',
        headers: { Authorization: `Bearer ${token}` },
      },
    )

    carregarHierarquia()
  }

  function toggle(list: number[], id: number, setter: any) {
    setter(list.includes(id) ? list.filter(i => i !== id) : [...list, id])
  }

  function abrirNovo() {
    setEditando(null)
    setForm({
      vinculacao_id: null,
      dpto_id: '',
      func_id: '',
      user_id: '',
      percentual_alocacao: 100,
      custo_mensal: '',
    })
    setShowModal(true)
  }

  function abrirEdicao(d: any, f: any, u: any) {
    setEditando(u)
    setForm({
      vinculacao_id: u.vinculacao_id,
      dpto_id: d.dpto_id,
      func_id: f.func_id,
      user_id: u.user_id,
      percentual_alocacao: u.percentual_alocacao,
      custo_mensal: u.custo_mensal ?? '',
    })
    setShowModal(true)
  }

  async function salvar() {
    const token = localStorage.getItem('entity_token')

    const url = editando
      ? `${process.env.NEXT_PUBLIC_API_URL}/entity/vinculacao/${form.vinculacao_id}`
      : `${process.env.NEXT_PUBLIC_API_URL}/entity/vinculacao`

    await fetch(url, {
      method: editando ? 'PUT' : 'POST',
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify(form),
    })

    setShowModal(false)
    carregarHierarquia()
  }

  return (
    <div>
      {/* restante do layout permanece igual */}

      {hierarquia.map(d =>
        d.funcoes.map(f =>
          f.usuarios.map((u: any) => (
            <div key={u.vinculacao_id}>
              {/* linha original */}
              <button onClick={() => excluir(u.vinculacao_id)}>
                <Trash2 size={14} />
              </button>
            </div>
          )),
        ),
      )}
    </div>
  )
}
